// import * as THREE from 'three';
import {
  Color,
  BufferGeometry,
  BufferAttribute,
  // PointsMaterial,
  ShaderMaterial,
  TextureLoader,
  Points,
  NormalBlending,
} from 'three';

import vertexShader from './parts.vert.glsl';
import fragmentShader from './parts.frag.glsl';

const MAX_COUNT = 2e6;
const COUNT = 1e6;
const SIZE = 15;

const SETS = {
  speed: .77,
  partSize: 10,
  noiseFreq: 1.,
  noiseAmp: 1.,
  parts: .2,
};

export default function volParts ({ gui, scene, camera, timer }) {

  camera.position.z = 8;

  scene.background = new Color('rgb(10, 30, 50)');

  gui.add(SETS, 'speed', 0, 3, .01).onChange(v => { timer.speed = Math.pow(v, 5) });
  gui.add(SETS, 'noiseFreq', 0, 3, .01).onChange(v => SETS.noiseFreq = Math.pow(v, 2));
  gui.add(SETS, 'noiseAmp', 0, 3, .01).onChange(v => SETS.noiseAmp = Math.pow(v, 2));;
  gui.add(SETS, 'parts', 0, MAX_COUNT / 1e6, .01).onChange(v => SETS.parts = Math.pow(v, 2));
  gui.add(SETS, 'partSize', .9, 10, .01);

  const geo = new BufferGeometry();
  const points = new Float32Array(MAX_COUNT * 3);
  const center = [-SIZE/2, SIZE/2];
  // const center = [0, 1];
  for (let i = 0; i < MAX_COUNT*3; i += 3) {
    points[i] = rand(center[0], center[1]);
    points[i+1] = rand(center[0], center[1]);
    points[i+2] = rand(center[0], center[1]);
  }

  geo.setAttribute('position', new BufferAttribute(points, 3));
  // const mat = new PointsMaterial({ color: 0xaa3388, size: .01 });
  let shader
   = new ShaderMaterial({
    uniforms: {
      time: { value: 0 },
      realTime: { value: timer.realTime },
      size: { value: SIZE },
      partSize: { value: SETS.partSize },
      freq: { value: SETS.noiseFreq },
      amp: { value: SETS.noiseAmp },
      tCircle: { value: new TextureLoader().load('circle.png') },
    },
    vertexShader,
    fragmentShader,

    transparent: true,
    blending: NormalBlending,
    // depthWrite: true,
    depthTest: false,
  });
  const mesh = new Points(geo, shader);

  scene.add(mesh);

  let noiseFreq = smooth(SETS.noiseFreq, 5);
  let noiseAmp = smooth(SETS.noiseAmp);
  let lastParts;
  return function onTick ({ timer }) {
    if (lastParts !== SETS.parts) {
      // geo.setAttribute('position', new BufferAttribute(points.slice(SETS.parts), 3));
      geo.setDrawRange(0, SETS.parts * 1e6)
      lastParts = SETS.parts;
    }

    shader.transparent = SETS.partSize <= 2;      

    // mesh.rotation.y += .0005;
    if (shader) {
      shader.uniforms.time.value = timer.elapsed / 1000;
      shader.uniforms.freq.value = noiseFreq(SETS.noiseFreq);
      shader.uniforms.amp.value = noiseAmp(SETS.noiseAmp);
      shader.uniforms.partSize.value = SETS.partSize;
      shader.uniforms.realTime.value = timer.realTime;
    }
  }
}

function smooth (val, asp = 30) {
  return function _smooth (v) {
    return val -= (val - v) / asp;
  }
}

function randCoord ([from, to]) {
  return [
    rand(from, to),
    rand(from, to),
    rand(from, to)
  ];
}

function rand (from = 0, to = 1) {
  return from + Math.random() * (to - from);
}