import {
    Scene,
    Color,
    PointLight,
    PerspectiveCamera,
    WebGLRenderer,
    Vector2,
} from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import * as dat from 'dat.gui';

import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';
import { AfterimagePass } from 'three/examples/jsm/postprocessing/AfterimagePass.js';
// import { FilmPass } from 'three/examples/jsm/postprocessing/FilmPass.js';
// import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
// import { PixelShader } from 'three/examples/jsm/shaders/PixelShader.js';

// import {
//     RenderPass,
//     UnrealBloomPass,
//     FilmPass,
// } from 'postprocessing';

// import go from './test1/go.js';
import go from './volParts/index.js';

// Debug
const gui = new dat.GUI()
gui.closed = true;

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new Scene();
scene.background = new Color('rgb(40,70, 100)');

// Lights

const pointLight = new PointLight(0xffffff, 1)
pointLight.position.x = 2
pointLight.position.y = 3
pointLight.position.z = 4
// scene.add(pointLight)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    composer.setSize(sizes.width, sizes.height)
    // renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    renderer.setPixelRatio(2)
})

/**
 * Camera
 */
// Base camera
const camera = new PerspectiveCamera(75, sizes.width / sizes.height, 0.00001, 1000);
camera.position.z = 2;
scene.add(camera)

/**
 * Renderer
 */
const renderer = new WebGLRenderer({
    canvas: canvas,
    antialias: true,
})
// console.log(renderer);
// renderer.toneMapping = ReinhardToneMapping;
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(2)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.listenToKeyEvents(window)
// console.log(controls)


const renderScene = new RenderPass( scene, camera );

const SETS = {
    // exposure: 1,
    bloomStrength: .77,
    bloomThreshold: .1,
    bloomRadius: 1.2,
    trails: .4,
    pixelRatio: 2,
};

gui.add(SETS, 'pixelRatio', .1, 2, .01).onChange(v => {
    renderer.setPixelRatio(v);
    // composer.setPixelRatio(v);
});
const bloomPass = new UnrealBloomPass( new Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
bloomPass.threshold = SETS.bloomThreshold;
bloomPass.strength = SETS.bloomStrength;
bloomPass.radius = SETS.bloomRadius;

gui.add(SETS, 'bloomStrength', 0, 3, .01).onChange(v => bloomPass.strength = v);

const afterimagePass = new AfterimagePass();
const setAfterImageDamp = (v) => ( afterimagePass.uniforms[ "damp" ].value = Math.pow(v, 1/3) );
setAfterImageDamp(SETS.trails);
gui.add(SETS, 'trails', 0, 1, .001).onChange(setAfterImageDamp);

let composer = new EffectComposer( renderer );
composer.addPass( renderScene );
composer.addPass( afterimagePass );
composer.addPass( bloomPass );
// composer.addPass( filmPass );

// const pixelPass = new ShaderPass( PixelShader );
// pixelPass.uniforms[ "resolution" ].value = new Vector2( window.innerWidth, window.innerHeight );
// pixelPass.uniforms[ "resolution" ].value.multiplyScalar( .3 );
// composer.addPass( pixelPass );

/**
 * Animate
 */

const now = performance ? () => performance.now() / 1e3 : () => Date.now() / 1e3;
const timer = new class Timer {
    constructor ({ speed = 1 } = {}) {
        this.speed = speed;

        this.restart();
    }
    
    restart () {
        const _now = now();
        this.startTime = _now;
        this.elapsed = 0;
        this.delta = 0;
        this.now = _now;
        this.lastNow = _now;
        this.update();
    }

    continue () {
        this.now = now() - this.delta;
    }

    update () {
        this.lastNow = this.now;
        this.now = now();
        this.realTime = this.now - this.startTime;
        this.delta = (this.now - this.lastNow) * this.speed;
        this.elapsed += this.delta;
    }
}

const onTick = go({ gui, scene, camera, timer });

let pause = false;
const toggleFullscreen = (function ToggleFullscreen ({ pointerLock } = {}) {
    let fullscreen = false;
    return function _toggleFullscreen (bool) {
        if (fullscreen = arguments.length ? bool : !fullscreen) {
            document.body.requestFullscreen();
            if (pointerLock)
                canvas.requestPointerLock();
            }
        else {
            document.exitFullscreen();
            if (pointerLock)
                document.exitPointerLock();
        }
    }
})({ pointerLock: false });
document.addEventListener('dblclick', () => toggleFullscreen());
window.addEventListener('keydown', e => {
    if (e.code === 'Space') {
        pause = !pause;
        if (!pause) {
            timer.continue();
            tick();
        }
    }
    else if (e.key === 'f')
        toggleFullscreen();
    else if (e.key === '/')
        timer.restart();
})

const fps = new class FPS {
    constructor () {
        this.$el = document.createElement('div');
        this.$el.id = 'fps';
        document.body.append(this.$el);
        this.ticksCount = 0;
        this.show = true;

        setInterval(() => {
            this.$el.style.display = this.show ? 'block' : 'none';
            this.$el.textContent = this.ticksCount + ' fps';
            this.ticksCount = 0;
        }, 1e3);
    }

    update () {
        this.ticksCount++;
    }
}

const tick = () => {
    timer.update();
    
    fps.show = !gui.closed;
    fps.update();

    onTick({ timer });

    controls.update()
    if (SETS.bloomStrength === 0 && SETS.trails === 0)
        renderer.render(scene, camera)
    else
        composer.render();

    if (pause)
        return;

    window.requestAnimationFrame(tick)
}
tick()